import React from 'react'

class ShopItem extends React.Component {
	render() {

		let content = this.props.content;

		let style = {
			overflow: 'hidden',
			display: '-webkit-box',
			textOverflow: 'ellipsis',
			WebkitLineClamp:'1',
			WebkitBoxOrient: 'vertical',
		}

		let star = <img src="/static/star-on.svg" style={{height: '14px'}}/>

		return (
			<li style={{backgroundColor: '#ffffff', padding: '6px 12px 0 0', marginBottom: '10px', borderRadius: '2px',	boxShadow: '0 2px 4px 0 rgba(163, 162, 162, 0.5)'}} onClick={this.props.onClick}>
				<div className="container-fluid">

					<div style={{display: 'flex'}} className="row">
						<div style={{width: '48px'}}>
							<img src={content.logo} style={{width: '100%'}} />
						</div>
						<div style={{flex: '1'}}>
							<p style={{fontSize: '15px', color: '#4a4a4a', marginBottom: '5px'}}>{content.name}</p>
							<div className="d-flex d-md-none" style={{display: 'flex'}}>
								<p style={Object.assign({}, style, {fontSize: '14px', color: '#767676',	lineHeight:'20px', margin: '0',	flex: '1'})}>{content.event[0].title}</p>
							</div>
							<div style={{position: 'relative'}}>
									{star}{star}{star}{star}{star}
								<div style={{position: 'absolute', right: '0', top: '0'}}>
									<img src="/static/pin.svg" style={{height: '12.7px'}}/>
									<span style={{fontSize: '14px', color: '#bf3829'}}>{content.distance}km</span>
								</div>
							</div>
							<img src="/static/next-page_2.svg" style={{position:'absolute', height: '15px', width: '8px', top:'50%', right: '13px', transform: 'translate(0, -50%)'}} />
						</div>
					</div>
					<div className="row">
						<div className="d-none d-md-block" style={{height: '252px'}}>
							<p style={Object.assign({}, style, {fontSize: '14px', color: '#767676',	lineHeight:'20px', margin: '0',	flex: '1'})}>{content.event[0].title}</p>
							<p style={{fontSize: '14px', color: '#767676'}}>{content.event[0].detail}</p>
						</div>
					</div>
				</div>
			</li>
		)
	}
}


export default ShopItem